:root {
  --main-font: "Roboto", sans-serif;
  --main-txt-color: #000;
  --secondary-txt-color: #606060;
  --logo-color: #d80000;
  --dark-grey-color: #606060;
  --grey-color: #d2d2d2;
  --light-grey-color: #f6f6f6;
  --white-color: #fff;
  --accent-color: #d4f2ff;
}

h1, h2, h3, h4, ul, p {
  margin: 0;
}

ul {
  padding-left: 0;
}

.list-no-marker {
  list-style: none;
}

.link-no-decor {
  text-decoration: none;
}

img {
  width: 100%;
  height: auto;
  display: block;
}

body {
  color: var(--main-txt-color);
  background-color: var(--main-bg-color);
  font-family: var(--main-font);
  letter-spacing: .03em;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.167;
}

.title {
  color: var(--dark-grey-color);
  margin-bottom: 30px;
  font-size: 20px;
  font-weight: 700;
  line-height: 1.2;
}

.subtitle {
  color: var(--main-txt-color);
  margin-bottom: 10px;
  font-size: 24px;
  font-weight: 700;
  line-height: 28px;
}

.text-content {
  color: var(--main-txt-color);
  font-size: 14px;
  font-weight: 400;
  line-height: 1.143;
}

.container {
  width: 320px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 15px;
  padding-right: 15px;
}

@media screen and (min-width: 480px) {
  .container {
    width: 420px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    width: 768px;
  }
}

@media screen and (min-width: 1000px) {
  .container {
    width: 1000px;
  }
}

.btn {
  text-align: center;
  cursor: pointer;
  color: var(--main-txt-color);
  background: #d4f2ff;
  border: 1px solid #d2d2d2;
  border-radius: 5px;
  padding: 6px 12px;
  font-size: 14px;
  font-weight: 500;
  line-height: 1.143;
}

.header__container {
  justify-content: space-between;
  align-items: center;
  padding-top: 14px;
  padding-bottom: 14px;
  display: flex;
}

.contacts {
  background-color: var(--accent-color);
}

.contacts__link-phone, .contacts__link-mail {
  color: var(--main-txt-color);
  font-size: 12px;
  font-style: normal;
  font-weight: 300;
  line-height: 1.167;
}

.contacts__link-mail {
  text-align: right;
}

.logo__container {
  padding-top: 26px;
  padding-bottom: 26px;
}

.logo__elips {
  width: 71px;
  height: 71px;
  background-color: var(--logo-color);
  border-radius: 50%;
  margin-bottom: 14px;
  margin-left: auto;
  margin-right: auto;
}

.logo__name {
  text-align: center;
  letter-spacing: .3em;
  text-transform: uppercase;
  color: var(--main-txt-color);
  font-size: 18px;
  font-weight: 700;
  line-height: 1.333;
}

.nav__list {
  justify-content: center;
  margin-left: auto;
  margin-right: auto;
  display: flex;
}

.nav__item {
  border-bottom: 1px solid #fff;
  transition: border-bottom .25s cubic-bezier(.4, 0, .2, 1);
}

.nav__item--active, .nav__item:hover, .nav__item:focus {
  background-color: var(--light-grey-color);
  border-bottom: solid 1px var(--logo-color);
}

.nav__link {
  text-align: center;
  text-transform: uppercase;
  color: var(--main-txt-color);
  padding: 26px 45px;
  font-size: 14px;
  font-weight: 300;
  line-height: 1.286;
  display: block;
}

.footer {
  background-color: var(--accent-color);
  padding: 21px 0;
}

.footer__wrapper {
  justify-content: space-between;
  display: flex;
}

.copyright {
  color: var(--main-txt-color);
  font-size: 12px;
  font-weight: 300;
  line-height: 1.167;
}

.main {
  background-color: var(--light-grey-color);
}

.main__container {
  padding-top: 40px;
  padding-bottom: 40px;
}

.codes-list {
  flex-wrap: wrap;
  align-content: space-between;
  gap: 22px;
  display: flex;
}

.codes-list__item {
  width: 226px;
  height: 480px;
  background-color: var(--white-color);
  border: 1px solid var(--grey-color);
  border-radius: 5px;
  flex-direction: column;
  justify-content: space-between;
  padding: 17px 17px 29px;
  display: flex;
}

.codes-list__img-thumbs {
  margin-bottom: 10px;
}

.codes-list__name {
  text-align: center;
  color: var(--main-txt-color);
  margin-bottom: 1px;
  font-size: 18px;
  font-weight: 500;
  line-height: 1.333;
}

.codes-list__part {
  text-align: center;
  color: var(--main-txt-color);
  font-size: 16px;
  font-weight: 500;
  line-height: 1.5;
}

.codes-list__order {
  text-align: center;
}

.codes-list__prise {
  text-align: center;
  color: var(--main-txt-color);
  margin-bottom: 11px;
  font-size: 18px;
  font-weight: 500;
  line-height: 1.333;
}

.codes-list__button {
  text-align: center;
  color: var(--main-txt-color);
  font-size: 14px;
  font-weight: 500;
  line-height: 1.143;
}

.wrapper {
  display: flex;
}

.contacts-page__list {
  width: 470px;
  border-right: 1px solid #d2d2d2;
  margin-right: 30px;
}

.contacts-page__item:not(:last-child) {
  margin-bottom: 10px;
}

.contacts-page__subtitle {
  color: var(--main-txt-color);
  margin-bottom: 6px;
  font-size: 16px;
  font-weight: 700;
  line-height: 1.5;
}

.contacts-page__descrip {
  color: var(--main-txt-color);
  margin-left: 20px;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.143;
}

.contacts-page__descrip--link {
  color: var(--main-txt-color);
  margin-left: 20px;
  line-height: 1.143;
}

.form-contacts {
  flex-direction: column;
  display: flex;
}

.form-contacts__wrapper {
  flex-direction: row;
  gap: 10px;
  display: flex;
}

.form-contacts__wrapper--name, .form-contacts__wrapper--mail {
  width: 220px;
  flex-direction: column;
  display: flex;
}

.form-contacts__label {
  color: var(--main-txt-color);
  margin-bottom: 6px;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.143;
}

.form-contacts__input, .form-contacts__textarea {
  border: 1px solid var(--grey-color);
  border-radius: 2px;
  margin-bottom: 10px;
  padding: 6px 8px;
}

.form-contacts__input::-ms-input-placeholder, .form-contacts__textarea::-ms-input-placeholder {
  color: var(--dark-grey);
  font-size: 12px;
  font-weight: 300;
  line-height: 1.333;
}

.form-contacts__input::placeholder, .form-contacts__textarea::placeholder {
  color: var(--dark-grey);
  font-size: 12px;
  font-weight: 300;
  line-height: 1.333;
}

.form-contacts__btn {
  background: var(--white-color);
  margin-left: auto;
}

/*# sourceMappingURL=codes-book.53589971.css.map */
